

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.header {
	background-color: #00838F;
	padding: 19px 0;
}

.container {
	min-width: 320px;
	width: 100%;
	padding: 0 32px;
	max-width: 1440px;
	margin: 0 auto;
}

.footer {
	background-color: #4F4F4F;
	height: 32px;
}
.title {
font-size: 46px;
font-weight: 400;
line-height: 1.43;
letter-spacing: 0.17px;
text-align: center;
color: #4F4F4F;
}

.main {
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}